<template>
  <div>

    <van-popup v-model="show" position="top" :overlay="true">
      内容
    </van-popup>

  <div class="amap-page-container" style="margin-bottom: 1%">
    <el-amap vid="amapDemo" :zoom="zoom" :plugin="plugin" :center="center" class="amap-demo" :style="{ height: screenHeight+ 'px' }">
      <el-amap-circle v-for="circle in circles" :center="circle.center" :radius="radius" :fillOpacity="fillOpacity"></el-amap-circle>
    </el-amap>
  </div>

    <div class="toolbar">
        <span v-if="loaded">
          location: lng = {{ lng }} lat = {{ lat }}
        </span>
      <span v-else>正在定位</span>
    </div>
  </div>
</template>

<style>

</style>
<script>
  import axios from 'axios'
  export default  {
    data () {
      let self = this;
      return {
        screenHeight:'',
        show:false,
        radius: 800,
        fillOpacity: 0.5,
        zoom: 10,
        center: [103.914983,30.812381],
        lng: 0,
        lat: 0,
        loaded: false,
        circles: [],

        plugin: [{
          pName: 'Geolocation',
          events: {
            init(o) {
              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                if (result && result.position) {
                  self.lng = result.position.lng;
                  self.lat = result.position.lat;
                  self.center = [self.lng, self.lat];
                  self.loaded = true;
                  axios.get('http://k8s.name:5000/position',{
                    params:{
                      lng:self.lng,
                      lat:self.lat}
                    }
                  )
                    .then(function(res){
                      console.log(res)
                    })
                    .catch(function(err){
                      console.log(err)
                    });
                  self.$nextTick();
                }
              });
            }
          }
        }]
      }
    },
    events: function(){
      var click = function () {
        this.show = true
      }
    },
    beforeMount:function () {
      //this.screenHeight = document.body.clientWidth-30
      this.screenHeight = 500
      var _this = this;
      axios.get('http://k8s.name:5000/all'
      )
        .then(function(res){
          _this.circles = res.data.data
          console.log(_this.circles)
        })
        .catch(function(err){
          console.log(err)
        });
    }
  };
</script>



